html,
body {
  position: relative;
  height: 100%;
}

body {
  // background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper_slide {
  text-align: center;
  font-size: 18px;
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper_slide img {
  display: block;
  width: 150px;
  height: 150px;
  object-fit: cover;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}
.container {
  max-width: 1090px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: auto;
  margin-right: auto;
}
.our_team_box {
  margin: auto;
}
.our_team_title {
  text-align: center;
  margin-top: 36px;
  font-size: 16px;
  font-family: 'Montserrat Alternates', sans-serif;
}
.our_team_line {
  background-color: #000000;
  height: 1px;
  width: 536px;
  margin: auto;
  margin-top: 36px;
}
.worker_img {
  width: 150px;
  height: 150px;
  border-radius: 20px;
  margin: auto;
}
.worker_name {
  font-size: 14px;
  font-family: 'Montserrat Alternates', sans-serif;
}
.worker_work {
  font-size: 12px;
  font-family: 'Montserrat Alternates', sans-serif;
  margin-bottom: 24px;
}
.swiper_button_next::after {
  color: black;
  --swiper-navigation-size: 90px;
}
.swiper_button_prev::after {
  color: black;
  --swiper-navigation-size: 90px;
}
